import { memo, useState, useEffect } from 'react';
import { Tabs } from 'antd-mobile';
import All from './components/all';
import './index.less';

const Home = () => {
  const [count, setCount] = useState('0');
  const topMenu = [
    { title: '全部', components: <All /> },
    { title: '点滴', components: 1 },
    { title: '相识', components: 2 },
    { title: '近期', components: 3 },
    { title: '往事', components: 4 },
    { title: '愿景', components: 5 },
    { title: '愿景2', components: 6 },
  ];
  useEffect(() => {});
  return (
    <div className="home_container">
      <div className="home_top">
        <Tabs
          activeKey={count}
          activeLineMode="fixed"
          style={{
            '--fixed-active-line-width': '20px',
          }}
          onChange={(e) => setCount(e + '')}>
          {topMenu.map((item, index) => (
            <Tabs.Tab title={item.title} key={index + ''}>
              {item.components}
            </Tabs.Tab>
          ))}
        </Tabs>
      </div>
    </div>
  );
};
export default memo(Home);
