import { useState, useEffect } from 'react';
import {
  Swiper,
  Toast,
  List,
  InfiniteScroll,
  DotLoading,
  Card,
} from 'antd-mobile';
import './index.less';

const All = () => {
  const colors = ['#ace0ff', '#bcffbd', '#e4fabd', '#ffcfac'];
  const tabList = [
    {
      image: '',
      title: 'wang',
    },
    {
      image: '',
      title: 'si',
    },
    {
      image: '',
      title: 'yv',
    },
  ];
  const bottomList = [
    {
      image: '',
      title: '这是一种悲伤的故事',
      tips: '若用标准libjpeg库，采用与dpg相同1111111111111111111122222222222222222222221111111111111111111111111111111111111111的q和采样因子压缩后，图片的大小为166549字节',
    },
    {
      image: '',
      title: '这是一种悲伤的故事',
      tips: '若用标准libjpeg库，采用与dpg相同的q和采样因子压缩后，图片的大小为166549字节',
    },
    {
      image: '',
      title: '这是一种悲伤的故事',
      tips: '若用标准libjpeg库，采用与dpg相同的q和采样因子压缩后，图片的大小为166549字节',
    },
    {
      image: '',
      title: '这是一种悲伤的故事',
      tips: '若用标准libjpeg库，采用与dpg相同的q和采样因子压缩后，图片的大小为166549字节',
    },
    {
      image: '',
      title: '这是一种悲伤的故事',
      tips: '若用标准libjpeg库，采用与dpg相同的q和采样因子压缩后，图片的大小为166549字节',
    },
  ];

  const [data, setData] = useState<any>(bottomList);
  const [hasMore, setHasMore] = useState(true);

  const loadMore = () => {
    setData((val: any) => [...val, ...bottomList]);
    setHasMore(data.length !== 10);
  };
  const onClickCard = (item: any) => {};

  useEffect(() => {});
  return (
    <div className="all_container">
      {/* 顶部轮播 */}
      <div className="all_banner">
        <Swiper autoplay loop>
          {colors.map((color, index) => (
            <Swiper.Item key={index}>
              <div
                style={{
                  background: color,
                }}
                onClick={() => {
                  Toast.show(`你点击了卡片 ${index + 1}`);
                }}>
                {index + 1}
              </div>
            </Swiper.Item>
          ))}
        </Swiper>
      </div>
      {/* 中间标签 */}
      <div className="all_tab">
        {tabList.map((item, index) => (
          <div className="all_tab_item" key={index}>
            <div className="all_tab_item_img"> </div>
            <div className="all_tab_item_title">{item.title}</div>
          </div>
        ))}
      </div>
      {/* 底部列表 */}
      <div className="all_list">
        <List>
          {data.map((item: any, index: number) => (
            <List.Item key={index}>
              <Card className="all_list_item" onClick={() => onClickCard(item)}>
                <div className="all_list_item_image"> </div>
                <div className="all_list_item_content">
                  <div className="all_list_item_title">{item.title}</div>
                  <div className="all_list_item_tips">{item.tips}</div>
                </div>
              </Card>
            </List.Item>
          ))}
        </List>

        <InfiniteScroll loadMore={async () => loadMore()} hasMore={hasMore}>
          {hasMore ? (
            <>
              <span>Loading</span>
              <DotLoading />
            </>
          ) : (
            <span>--- 我是有底线的 ---</span>
          )}
        </InfiniteScroll>
      </div>
    </div>
  );
};
export default All;
